<template>
    <div>
        <el-input style="width: 200px;margin-right:30px;" v-model="tagName"></el-input>
        <el-button @click="addTag()" type="primary" style="margin-bottom: 30px;">新增标签</el-button>
        <el-table :data="tagList" border style="width: 100%">
            <el-table-column prop="_id" label="序号" header-align="center" align="center"></el-table-column>
            <el-table-column prop="name" label="标签名称" header-align="center" align="center"></el-table-column>
            <el-table-column label="操作" header-align="center" align="center">
                <template slot-scope="scope">
                    <el-button @click="deleteTag(scope.row)" type="danger" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    import Axios from 'axios'
    export default {
        data() {
            return {
                tagList: [],
                tagName: ''
            }
        },
        mounted() {
            this.showTagList()
        },
        methods: {
            showTagList() {
                Axios({
                    url: '/tag',
                    method: 'get'
                }).then( res => {
                    console.log(res.data.data)
                    this.tagList = res.data.data
                })
            },
            addTag() {
                Axios({
                    url: '/tag',
                    method: 'post',
                    data: {
                        name: this.tagName
                    }
                }).then( res => {
                    this.$message({
                        type: 'success',
                        message: '添加成功'
                    })
                    this.showTagList()
                })
            },
            deleteTag(row) {
                let id = row._id
                Axios({
                    url: '/tag/' + id,
                    method: 'delete'
                }).then( res => {
                    this.$message({
                        type: 'success',
                        message: '删除成功'
                    })
                    this.showTagList()
                })
            }
        }
    }
</script>


