<template>
    <div class="add-message">
        <el-button @click="add()" type="primary" v-if="!isShow" style="margin-bottom: 30px;">新增参数二维码</el-button>
        <el-table :data="tableData" border style="width: 100%" v-if="!isShow">
            <el-table-column prop="name" label="二维码名称"></el-table-column>
            <el-table-column prop="content" label="关注后回复内容"></el-table-column>
            <el-table-column prop="gonghao" label="公号"></el-table-column>
            <el-table-column prop="qr_code_url" label="二维码" width="120">
                <template slot-scope="scope">
                    <img class="qr_img" :src="scope.row.qr_code_url" />
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button @click="update(scope.row)" type="primary" size="small">修改</el-button>
                    <el-button @click="deleteOne(scope.row)" type="danger" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="wrapper" v-if="isShow">
            <h3 class="wrap-title">添加/修改二维码</h3>
            <el-form label-width="120px" size="mini">
                <el-form-item label="二维码名称" prop="name">
                    <el-input v-model="name" style="width:300px;"></el-input>
                </el-form-item>
                <el-form-item label="关注回复内容" prop="content">
                    <el-input type="textarea" rows="5" v-model="content" style="width:300px;"></el-input>
                </el-form-item>
                <el-form-item label="选择标签" prop="tagName">
                    <div class="tag-name">
                        <el-tag closable @close="handleClose()" size="mini" v-model="tagName" v-if="tagName">{{tagName}}</el-tag>
                        <input type="text" v-model="tagVal"  @keyup.enter="change">
                    </div>
                </el-form-item>
                <el-form-item v-if="tagList != []">
                    <ul class="tag-list">
                        <li v-for="(item, index) in tagList" :key="index" @click="select(item)">
                            <el-tag>{{item.name}}</el-tag>
                        </li>
                    </ul>
                </el-form-item>
                <el-form-item label="选择公号">
                    <el-radio-group v-model="code">
                        <el-radio :disabled="isUpdate" style="margin: 10px;" v-for="(item, index) in gonghaoList" :key="index" :label="item.code">{{item.name}}</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item size="large">
                    <el-button type="primary" @click="save" v-if="isUpdate">保存</el-button>
                    <el-button type="primary" @click="create" v-else>立即生成</el-button>
                    <el-button type="info" @click="cancel">取消</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import Axios from 'axios'
    export default {
        data() {
            return {
                name: '',
                content: '',
                gonghao: '',
                code: '',
                qr_code_url: '',
                gonghaoList: [],
                tableData: [],
                isUpdate: false,
                isShow: false,
                id: '',
                tagName: '',
                tagVal: '',
                tagList: [],
                tagId: ''
            }
        },
        mounted(){
            this.showList()
            this.getGonghaoList()
            this.showTagList()
        },
        methods: {
            handleClose() {
                this.tagName = ''  
            },
            showTagList() {
                Axios({
                    url: '/tag',
                    method: 'get'
                }).then(res => {
                    this.tagList = res.data.data
                })
            },
            change() {
                if(this.tagVal == '') {
                    this.$message.error('标签名不能为空')
                } else {
                    Axios({
                        url: '/tag',
                        method: 'post',
                        data: {
                            name: this.tagVal
                        }
                    }).then(res => {
                        this.tagName = this.tagVal
                        this.tagId = res.data.data._id
                        this.tagVal = ''
                        this.showTagList()
                    })
                }
            },
            select(item) {
                Axios({
                    url: '/tag',
                    method: 'post',
                    data: {
                        name: item.name
                    }
                }).then(res => {
                    this.tagId = res.data.data._id
                    this.tagName = res.data.data.name
                })
            },
            add() {
                this.isShow = true
            },
            cancel() {
                this.name = this.content = this.code = ''
                this.isShow = false
            },
            create() {
                Axios({
                    url: '/qr_code/create',
                    method: 'post',
                    data: {
                        name: this.name,
                        content: this.content,
                        code: this.code,
                        tagId: this.tagId
                    }
                }).then(res => {
                    if(res.data.data == '') {
                        this.$message.error('创建失败')
                    } else {
                        console.log(res.data)
                        this.$message({
                            type: 'success',
                            message: '创建成功'
                        })
                        this.showList()
                        this.name = this.content = this.code = ''
                        this.isShow = false
                    }
                })
            },
            getGonghaoList() {
                Axios({
                    url: '/qr_code/get_code',
                    method: 'get'
                }).then(res => {
                    this.gonghaoList = res.data.codes
                })
            },
            showList() {
                Axios({
                    url: '/qr_code/show',
                    method: 'get'
                }).then(res => {
                    var codes = res.data.codes
                    var data = res.data.data
                    for(var i=0,codesLength=codes.length;i<codesLength;i++) {
                        for(var j=0,dataLength=data.length;j<dataLength;j++){
                            if(codes[i].code == data[j].code) {
                                data[j].gonghao = codes[i].name
                            }
                        }
                    }
                    this.tableData = data
                })
            },
            update(row) {
                this.isUpdate = true
                this.name = row.name
                this.content = row.content
                this.code = row.code
                this.qr_code_url = row.qr_code_url
                this.isShow = true
                this.id = row._id
                this.tagId = row.tagId
                Axios({
                    url: '/tag/get_name',
                    method: 'get',
                    params: {
                        tagId: this.tagId
                    }
                }).then(res => {
                    this.tagName = res.data.data.name
                })
            },
            deleteOne(row) {
                Axios({
                    url: '/qr_code/deleteOne',
                    method: 'post',
                    data: {
                        id: row._id
                    }
                }).then(res => {
                    this.$message({
                        type: 'success',
                        message: res.data.success
                    })
                    if(res.data.data == '') {
                        this.tableData = []
                    } else {
                        this.showList()
                    }
                })
            },
            save() {
                Axios({
                    url: '/qr_code/update',
                    method: 'post',
                    data: {
                        id: this.id,
                        name: this.name,
                        content: this.content,
                        code: this.code,
                        tagId: this.tagName == '' ? '' : this.tagId
                    }
                }).then(res => {
                    if(res.data.data == '') {
                        this.$message.error('修改失败')
                    } else {
                        this.$message({
                            type: 'success',
                            message: '修改成功'
                        })
                        this.showList()
                        this.name = this.content = this.code = ''
                        this.isShow = false
                    }
                })
            }
        }
    };
</script>

<style scoped>
    .wrapper{
        margin-top: 30px;
        border: 1px solid #eee;
    }
    
    .wrap-title{
        padding: 20px;
        text-align: center;
    }
    
    .qr_img {
        width: 100px;
        height: 100px;
    }

    .tag-name {
        width: 150px;
        height: 30px;
        line-height: 30px;
        border: 1px solid #eee;
        border-radius: 5px;
        padding-left: 10px;
    }
    .tag-name input {
        padding-left: 0;
        width: 80px;
        border: 0;
        outline: none;
    }
    .tag-list li{
        float: left;
        margin-right: 10px;
        cursor: pointer;
    }
</style>




