<template>
    <div class="data-statistics">
        <el-collapse v-model="activeModel">
            <el-collapse-item name='1'>
                <template slot="title">
                    <span class="select-gonghao">选择公号</span>
                </template>
                <el-radio-group v-model="gonghao" size="mini">
                    <el-radio class="gonghao" v-for="(item, index) in codes" :key="index" :label="item.code" border>{{item.name}}</el-radio>
                </el-radio-group>
                <el-button @click="search()" type="primary" plain style="margin-top: 30px;">确定</el-button>
            </el-collapse-item>
        </el-collapse>
        <div class="title">
            <span class="name">公号名称： <span>{{obj.name}}</span></span><br />
            <span>今日总新增关注用户数： <span>{{obj.subscribe_count}}</span></span>
            <span>今日总新增中的取消关注用户数： <span>{{obj.unsubscribe_count}}</span></span>
            <span>今日总新增用户净增长数： <span>{{obj.growth}}</span></span>
        </div>
        <el-table class="el-fade-in-linear" stripe height="400" show-summary :data="dataList" border style="width: 100%;margin-top: 10px;">
            <el-table-column align="center" header-align="center" prop="date" label="时间"></el-table-column>
            <el-table-column align="center" header-align="center" prop="subscribe_count" label="今日新增关注用户数"></el-table-column>
            <el-table-column align="center" header-align="center" prop="unsubscribe_count" label="今日新增中的取消关注用户数"></el-table-column>
            <el-table-column align="center" header-align="center" prop="growth" label="今日新增用户净增长数"></el-table-column>
        </el-table>
        <div class="my-chart" id="myChart" :style="{width: '100%', height: '500px'}"></div>
    </div>
</template>

<script>
import Axios from 'axios';
export default {
    data() {
        return {
            dataList: [],
            gonghao: '1',
            codes: [],
            activeModel: '0',
            obj: {}
        }
    },
    mounted() {
        this.getCodes()
        this.search()
    },
    methods: {
        drawLine(){
        // 基于准备好的dom，初始化echarts实例
            let myChart = this.$echarts.init(document.getElementById('myChart'))
            let date = [], subscribe_count = [], unsubscribe_count = [], growth = [], dataList = this.dataList;
            for (var i=0; i < dataList.length; i ++) {
                date.unshift(dataList[i].date)
                subscribe_count.unshift(dataList[i].subscribe_count)
                unsubscribe_count.unshift(dataList[i].unsubscribe_count)
                growth.unshift(dataList[i].growth)
            }
            // 绘制图表
            myChart.setOption({
                title : {
                    text: '今日新增用户统计折线图'
                },
                tooltip : {
                    trigger: 'axis'
                },
                legend: {
                    data:['今日新增关注用户数','今日新增中的取消关注用户数','今日新增用户净增长数']
                },
                toolbox: {
                    show : true,
                    feature : {
                        mark : {show: true},
                        magicType : {show: true, type: ['line', 'bar']},
                        restore : {show: true},
                        saveAsImage : {show: true}
                    }
                },
                calculable : false, // 是否拖拽
                xAxis : [
                    {
                        type : 'category',
                        boundaryGap : false,
                        data : date
                    }
                ],
                yAxis : [
                    {
                        type : 'value'
                    }
                ],
                series : [
                    {
                        name:'今日新增关注用户数',
                        type:'line',
                        data: subscribe_count,
                    },
                    {
                        name:'今日新增中的取消关注用户数',
                        type:'line',
                        data:unsubscribe_count,
                    },
                    {
                        name:'今日新增用户净增长数',
                        type:'line',
                        data:growth,
                    }
                ]
            })
        },
        search() {
            this.dataList = []
            this.activeModel = '0'
            this.obj = {}
            this.$message({
                type: 'info',
                message: '查询数据库需要几秒钟的时间，请耐心等待'
            })
            let code = this.gonghao
            Axios({
                url: '/statistics/group/' + code,
                method: 'get'
            }).then(res => {
                // console.log(res.data.data.list)
                this.dataList = res.data.data.list
                this.obj.name = res.data.data.name
                this.obj.subscribe_count = res.data.data.subscribe_count
                this.obj.unsubscribe_count = res.data.data.unsubscribe_count
                this.obj.growth = res.data.data.growth
                this.drawLine()
                this.chartShow = true
            })
        },
        getCodes() {
            Axios({
                url: '/statistics/get_code',
                method: 'get',
            }).then(res => {
                this.codes = res.data.codes
            })
        }
    }
}
</script>

<style scoped>
.data-statistics {
    height: 100vh;
    padding-bottom: 50px;
}
.title {
    margin-top: 30px;
    font-family: '楷体';
    margin-left: 10px;
}

.name {
    display: block;
}

.title>span {
    margin-right: 30px;
}

.title>span span {
    color: rgb(0, 159, 255);
    font-weight: bold;
}

.select-gonghao {
    color: rgb(0, 159, 255);
    font-family: '楷体';
    font-size: 20px;
}
.gonghao {
    margin-right: 15px;
    margin-top: 20px;
    margin-left: 0 !important;
}

.my-chart {
    margin-top: 30px;
}
</style>



