<template>
    <div class="home">
        <el-menu :default-active="activeIndex" mode="horizontal" @select="aaa">
            <el-menu-item index="1">爆款单页</el-menu-item>
            <el-menu-item index="2">参数二维码</el-menu-item>
            <el-menu-item index="3">小说链接</el-menu-item>
            <el-menu-item index="4">随机链接</el-menu-item>
            <el-menu-item index="5">标签管理</el-menu-item>
			<el-menu-item index="6">数据统计</el-menu-item>
			<el-menu-item index="7">支付宝链接</el-menu-item>
        </el-menu>
        <div class="wrapper">
            <baokuan v-if="activeIndex == '1'"></baokuan> 
            <qr-code v-if="activeIndex == '2'"></qr-code>
            <novel-link v-if="activeIndex == '3'"></novel-link>
            <random-links v-if="activeIndex == '4'"></random-links>
            <tag-management v-if="activeIndex == '5'"></tag-management>
			<data-statistics v-if="activeIndex == '6'"></data-statistics>
			<alipay v-if="activeIndex == '7'"></alipay>
        </div>
    </div>
</template>

<script>
    import baokuan from '../baokuan/baokuan.vue'
    import qrCode from '../novel/qrCode/qrCode.vue'
    import tagManagement from '../novel/qrCode/tagManagement.vue'
    import novelLink from '../novel/novelLink/novelLink.vue'
    import randomLinks from '../novel/randomLinks/randomLinks.vue'
	import dataStatistics from '../novel/dataStatistics/dataStatistics.vue'
	import alipay from '../novel/alipay/alipay.vue'
    export default {
        components: {
            baokuan,
            qrCode,
            novelLink,
            randomLinks,
            tagManagement,
            dataStatistics,
            alipay
        },
        data() {
            return {
                activeIndex: '1',
            }
        },
        mounted(){
        },
        methods: {
            aaa(key, value) {
                this.activeIndex = key
            }
        }
    }
</script>

<style scoped>
.home {
    padding: 20px 100px;
}
.wrapper {
    padding: 20px 0;
}
</style>



