<template>
    <div class="alipay">
        <el-form ref="form" :model="form" label-width="100px" size="mini">
            <el-form-item label="链接标题" class="forn-item">
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="链接" class="forn-item">
                <el-input v-model="form.link"></el-input>
            </el-form-item>
            <el-form-item size="mini" class="forn-item">
                <el-button type="success" @click="addOne" s>添加一条</el-button>
            </el-form-item>
        </el-form>
        <el-table class="el-fade-in-linear" stripe :data="linkList" border style="width: 100%;margin-top: 10px;">
            <el-table-column align="center" header-align="center" prop="name" label="链接标题"></el-table-column>
            <el-table-column align="center" header-align="center" prop="link" label="链接"></el-table-column>
            <el-table-column align="center" header-align="center" label="操作">
                <template slot-scope="scope">
                    <el-button @click="deleteOne(scope.row, scope.$index)" type="danger" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
import Axios from 'axios'
export default {
    data() {
        return {
            form: {},
            linkList: []
        }
    },
    mounted() {
        this.showList()
    },
    methods: {
        addOne() {
            Axios({
                url: '/alipayLink',
                method: 'post',
                data: this.form
            }).then(res => {
                this.linkList.push(this.form)
                this.$message({
                    type: 'success',
                    message: res.data.success
                })
                this.form = {}
            })
        },
        showList() {
            Axios({
                url: '/alipayLink',
                method: 'get'
            }).then(res => {
                this.linkList = res.data.data
            })
        },
        deleteOne(row, index) {
            Axios({
                url: '/alipayLink/del',
                method: 'get',
                params: {id: row._id}
            }).then(res => {
                this.$message({
                    type: 'success',
                    message: res.data.success
                })
                this.linkList.splice(index, 1)
            })
        }
    }
}
</script>

<style scoped>
.forn-item {
    float: left;
    width: 400px;
}
.forn-item:last-child {
    width: 200px;
    margin-left: -50px;
}
</style>



