<template>
    <div class="novel-link">
        <el-button @click="add()" type="primary" v-if="!isShow" style="margin-bottom: 30px;">新增小说链接</el-button>
        <label style="margin-left: 30px;" v-if="!isShow">淘口令：<el-input v-model="tokenArr" style="width:300px;"></el-input></label>
        <el-button :disabled="tokenArr == ''" @click="updateTokens()" type="success" v-if="!isShow">更新淘口令</el-button>
        <el-table :data="tableData" border style="width: 100%;" v-if="!isShow">
            <el-table-column align="center" header-align="center" prop="id" label="id" width="60"></el-table-column>
            <el-table-column align="center" header-align="center" prop="type" label="类型" width="100">
                <template slot-scope="scope">
                    {{scope.row.type == 0 ? '微跳' : (scope.row.type == 1 ? '非微调单页' : '非微调多页')}}
                </template>
            </el-table-column>
            <el-table-column align="center" header-align="center" prop="name" label="公号" :width="80"></el-table-column>
            <el-table-column align="center" header-align="center" prop="picurl" label="图片" width="200">
                <template slot-scope="scope">
                    <img class="beauty" :src="domain_names[0].domain_name + scope.row.picurl" />
                </template>
            </el-table-column>
            <el-table-column header-align="center" prop="channel" label="渠道" width="100"></el-table-column>
            <el-table-column header-align="center" prop="remarks" label="备注"></el-table-column>
            <el-table-column align="center" header-align="center" label="复制链接" width="120">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.type == 0" :data-clipboard-text="domain_names[0].domain_name + '/tuiguang/weitiao/' + scope.row.id" class="copyBtn" @click="copy" type="success" size="small">微跳</el-button>
                    <el-button v-if="scope.row.type == 1" :data-clipboard-text="domain_names[0].domain_name + '/tuiguang/singlepage/' + scope.row.id" class="copyBtn" @click="copy" type="success" size="small">非微调单页</el-button>
                    <el-button v-if="scope.row.type == 2" :data-clipboard-text="domain_names[0].domain_name + '/tuiguang/multipage/' + scope.row.id" class="copyBtn" @click="copy" type="success" size="small">非微调多页</el-button>
                </template>
            </el-table-column>
            <el-table-column align="center" header-align="center" label="操作" width="150">
                <template slot-scope="scope">
                    <el-button @click="update(scope.row)" type="primary" size="small">修改</el-button>
                    <el-button @click="deleteOne(scope.row)" type="danger" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-form ref="form" :model="form" label-width="100px" size="mini" v-if="isShow">
            <el-form-item label="类型">
                 <el-radio-group v-model="form.type">
                    <el-radio :label="0">微跳</el-radio>
                    <el-radio :label="1">非微调单页</el-radio>
                    <el-radio :label="2">非微调多页</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="id">
                <el-input :disabled="isUpdate" v-model="form.id" style="width:100px;"></el-input>
            </el-form-item>
            <el-form-item label="页面标题">
                <el-input v-model="form.pageTitle" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="文章标题">
                <el-input v-model="form.articleTit" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="公号名称">
                <el-input v-model="form.name" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="公号描述">
                <el-input v-model="form.desc" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="图片" prop="imageFile">
                <el-upload :show-file-list="false" enctype="multipart/form-data" name="imageFile" action="http://www.rrdtjj.top/tuiguang/novel/upload" :on-success="handleAvatarSuccess">
                    <el-button size="small" type="primary">点击上传</el-button>
                    <img class="b_img" v-if="imgUrl" :src="imgUrl" alt="" width="200">
                </el-upload>
            </el-form-item>
            <el-form-item label="文章内容" prop="content">
                <el-input type="textarea" v-model="form.capter1" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item v-if="form.type == 2" label="文章内容(第二章)" prop="content">
                <el-input type="textarea" v-model="form.capter2" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item v-if="form.type == 0" label="跳转链接">
                <el-input v-model="form.linkUrl" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="统计链接">
                <el-input v-model="form.statisticsUrl1" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item v-if="form.type == 2" label="统计链接(第二章)">
                <el-input v-model="form.statisticsUrl2" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item v-if="form.type != 1" label="口令代码">
                <el-input v-model="form.tokenCodes" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="渠道">
                <el-input v-model="form.channel" style="width:150px;"></el-input>
            </el-form-item>
            <el-form-item label="备注">
                <el-input v-model="form.remarks" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item size="large">
                <el-button v-if="!isUpdate" type="primary" @click="create">立即创建</el-button>
                <el-button v-else type="primary" @click="save">保 存</el-button>
                <el-button v-if="!isUpdate" type="info" @click="reset">重 置</el-button>
                <el-button type="danger" @click="cancel">取 消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import Axios from 'axios'
    import Clipboard from 'clipboard';
    let clipboard = new Clipboard('.copyBtn');
    export default {
        data() {
            return {
                form: {
                    type: 0,
                    pageTitle: '冒死推荐，多看一本是一本',
                    articleTit: ' 女人最多能承受多少厘米',
                    desc: '一个陪你过夜的小说站'
                },
                imgUrl: '',
                isShow: false,
                tableData: [],
                isUpdate: false,
                domain_names: [],
                tokenArr: ''
            };
        },
        mounted() {
            this.show()
        },
        methods: {
            updateTokens() {
                this.$confirm('确定要修改淘口令吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    Axios({
                        url: '/tuiguang/token_arr',
                        method: 'get',
                        params: {
                            tokenArr: this.tokenArr
                        }
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: res.data.success
                        })
                        this.tokenArr = ''
                    })
                }).catch(() => {});
                
            },
            getContent(id) {
                Axios({
                    url: '/tuiguang/novel/get_content',
                    method: 'get',
                    params: {
                        _id: id
                    }
                }).then(res => {
                    this.$set(this.form,'capter1',res.data.data.capter1)
                    this.$set(this.form,'capter2',res.data.data.capter2)
                })
            },
            add() {
                this.isShow = true
            },
            handleAvatarSuccess(res, file) {
                this.imgUrl = this.domain_names[0].domain_name + '/images/tuiguang/' + res.filename
                this.form.picurl = '/images/tuiguang/' + res.filename
            },
            create() {
                this.$confirm('小哥哥/小姐姐，确定要创建一条新的链接嘛?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    Axios({
                        url: '/tuiguang/novel/add',
                        method: 'post',
                        data: {
                            type: this.form.type,
                            id: this.form.id,
                            pageTitle: this.form.pageTitle,
                            articleTit: this.form.articleTit,
                            name: this.form.name,
                            desc: this.form.desc,
                            picurl: this.form.picurl,
                            capter1: this.form.capter1,
                            capter2: this.form.capter2,
                            linkUrl: this.form.linkUrl,
                            statisticsUrl1: this.form.statisticsUrl1,
                            statisticsUrl2: this.form.statisticsUrl2,
                            channel: this.form.channel,
                            tokenCodes: this.form.tokenCodes,
                            remarks: this.form.remarks,
                        }
                    }).then(res => {
                        if(res.data.err) {
                            this.$message.error(res.data.err)
                        } else {
                            this.$message({
                                type: 'success',
                                message: res.data.message
                            })
                            this.form = {
                                type: 0,
                                pageTitle: '冒死推荐，多看一本是一本',
                                articleTit: ' 女人最多能承受多少厘米',
                                desc: '一个陪你过夜的小说站'
                            }
                            this.imgUrl = ''
                            this.isShow = false
                            this.show()
                        }
                    })
                }).catch(() => {});
                
            },
            reset() {
                this.$confirm('兄弟/妹子，你可想好了，确定要清空已经填好的信息吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.form = {
                        type: 0,
                        pageTitle: '冒死推荐，多看一本是一本',
                        articleTit: ' 女人最多能承受多少厘米',
                        desc: '一个陪你过夜的小说站'
                    }
                }).catch(() => {});
            },
            deleteOne(row) {
                this.$confirm('小哥哥/小姐姐，你真的不要我了吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    Axios({
                        url: '/tuiguang/novel/delete_one',
                        method: 'post',
                        data: {
                            id: row.id
                        }
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: res.data.message
                        })
                        this.show()
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '你们还是爱我的，么么哒~~~'
                    })
                });
            },
            copy() {
                this.$message({
                    type: 'success',
                    message: '复制成功'
                })
            },
            show() {
              Axios({
                url: '/tuiguang/novel/show',
                method: 'get'
              }).then(res => {
                  this.tableData = res.data.data
                  this.domain_names = res.data.domain_names
              })
            },
            update(row) {
                this.form = row
                this.isUpdate = this.isShow = true
                this.imgUrl = this.form.picurl
                this.getContent(row._id)
            },
            cancel() {
                this.isShow = this.isUpdate = false
                this.form = {
                    type: 0,
                    pageTitle: '冒死推荐，多看一本是一本',
                    articleTit: ' 女人最多能承受多少厘米',
                    desc: '一个陪你过夜的小说站'
                }
                this.imgUrl = ''
            },
            save() {
                this.$confirm('小哥哥/小姐姐，你们要把握变得更加漂亮吗?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    Axios({
                        url: '/tuiguang/novel/update',
                        method: 'post',
                        data: {
                            _id: this.form._id,
                            type: this.form.type,
                            id: this.form.id,
                            pageTitle: this.form.pageTitle,
                            articleTit: this.form.articleTit,
                            name: this.form.name,
                            desc: this.form.desc,
                            picurl: this.form.picurl,
                            capter1: this.form.capter1,
                            capter2: this.form.capter2,
                            linkUrl: this.form.linkUrl,
                            statisticsUrl1: this.form.statisticsUrl1,
                            statisticsUrl2: this.form.statisticsUrl2,
                            tokenCodes: this.form.tokenCodes,
                            channel: this.form.channel,
                            remarks: this.form.remarks,
                        }
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: res.data.success
                        })
                        this.form = {
                            type: 0,
                            pageTitle: '冒死推荐，多看一本是一本',
                            articleTit: ' 女人最多能承受多少厘米',
                            desc: '一个陪你过夜的小说站'
                        }
                        this.imgUrl = ''
                        this.isShow = this.isUpdate = false
                    })
                }).catch(() => {});
                
            }
        }
    };
</script>

<style scoped>
.beauty {
    width: 180px;
}

.b_img {
    margin-top: 10px;
}
</style>




